import React, {Component} from 'react';
import {Link} from 'react-router-dom';
// import {   my_stor } from "../../../../../router/My_router";
import Item from './items.js';
class App extends Component {
    constructor(props) {
        super(props);
        this.state = {
            st: 'l1' + this.props.id,
            items: '',
            currency: 'all',
        };
        this.activateLasers = this.activateLasers.bind(this);
        this.handleChange = this.handleChange.bind(this);
        this.m_item_fun = this.m_item_fun.bind(this);
    }
    activateLasers(e) {
        this.setState({
            st: e.target.dataset.name,
            currency: e.target.dataset.currency,
        });
        /* console.log( a.target.classList.toggle( String,  )); */
    }
    handleChange(e) {
        this.setState({items: e.target.value});
    }
    m_item_fun() {
        if (this.state.currency === 'all') {
            let tt = {key: {}, arey: []};
            for (let a in this.props.m_item) {
                this.props.m_item[a].map(a_1 => {
                    if (!tt.key[a_1.text_1]) {
                        tt.key[a_1.text_1] = true;
                        tt.arey.push(a_1);
                    }
                    return '';
                });
            }

            return tt.arey;
        } else {
            if (this.props.m_item[this.state.currency]) {
                return this.props.m_item[this.state.currency];
            } else {
                console.error(this.state.currency, 'm_item_fun');
            }
        }
    }
    render() {
        let arrey_currency = this.m_item_fun();
        let htmlFor = this.props.htmlFor || '',
            placeholder_1 = this.props.placeholder_1 || '',
            placeholder_2 = this.props.placeholder_2 || '',
            placeholder_3 = this.props.placeholder_3 || '',
            placeholder_4 = this.props.placeholder_4 || '',
            column_title = this.props.column_title || '',
            m_title = this.props.m_title || '',
            text_1 = this.props.text_1 || '',
            text_2 = this.props.text_2 || '',
            id = this.props.id || '';
        return (
            <div className="tab">
                <div className="tab-content">
                    <div className="exchange-currency">
                        <div
                            className={`tab-min ${this.state.st === 'l1' + id ? 'tab_active' : ''}`}
                            data-currency="all"
                            data-name={'l1' + id}
                            onClick={this.activateLasers}
                        >
                            <p data-name={'l1' + id} className="" data-currency="all" onClick={this.activateLasers}>
                                {htmlFor}
                            </p>
                        </div>
                        <div
                            className={`tab-min_1 ${this.state.st === 'l2' + id ? 'tab_active' : ''}`}
                            id={'l2' + id}
                            data-name={'l2' + id}
                            onClick={this.activateLasers}
                            data-currency="RUB"
                        >
                            <p data-name={'l2' + id} data-currency="RUB" onClick={this.activateLasers}>
                                RUB
                            </p>
                        </div>
                        <div
                            className={`tab-min_1 ${this.state.st === 'l3' + id ? 'tab_active' : ''}`}
                            data-name={'l3' + id}
                            id={'l3' + id}
                            onClick={this.activateLasers}
                            data-currency="USD"
                        >
                            <p data-name={'l3' + id} data-currency="USD" onClick={this.activateLasers}>
                                USD
                            </p>
                        </div>
                        <div
                            className={`tab-min_2 ${this.state.st === 'l4' + id ? 'tab_active' : ''}`}
                            id={'l4' + id}
                            onClick={this.activateLasers}
                            data-name={'l4' + id}
                            data-currency="COIN"
                        >
                            <p data-name={'l4' + id} data-currency="COIN" onClick={this.activateLasers}>
                                COIN
                            </p>
                        </div>
                    </div>
                    <div className="search-currency">
                        <input
                            type="text"
                            placeholder={placeholder_1}
                            onChange={this.handleChange}
                            value={this.state.items}
                        />
                    </div>
                    <br />
                    <div className="column-title">{column_title}</div>
                    <div className="scrollbar">
                        <div className="items">
                            {(Array.isArray(arrey_currency) ? arrey_currency : [])
                                .sort()
                                .map((a, b) => {
                                    let test = (
                                        <Item
                                            key={b}
                                            src={a.src}
                                            text_1={a.text_1}
                                            text_2={a.text_2}
                                            number={a.number}
                                        />
                                    );
                                    if (!this.state.items) {
                                        return test;
                                    }
                                    if (new RegExp(this.state.items, 'mi').test(a.text_1)) {
                                        return test;
                                    }
                                    return '';
                                })}
                        </div>
                    </div>
                    <Link to="#" className="js-scroll-down scroll-down">
                        <svg xmlns="http://www.w3.org/2000/svg" width={26} height={22} viewBox="0 0 26 22">
                            <path
                                fill="#343844"
                                fillRule="nonzero"
                                d="M2.219.688L.78 2.093l11.5 11.5.719.719.719-.72 11.5-11.5L23.78.689 13 11.467 2.219.689zm0 7L.78 9.094l11.5 11.5.719.718.719-.718 11.5-11.5-1.438-1.406L13 18.468 2.219 7.689z"
                            />
                        </svg>
                    </Link>
                    <div className="empty-search">
                        <svg
                            className="search-icon"
                            xmlns="http://www.w3.org/2000/svg"
                            width={49}
                            height={49}
                            viewBox="0 0 49 49"
                        >
                            <g fill="#B4B4B7" fillRule="evenodd">
                                <path
                                    fillRule="nonzero"
                                    d="M29.948 0C19.447 0 10.896 8.551 10.896 19.052c0 4.562 1.6 8.745 4.286 12.027L0 46.261 2.739 49 17.92 33.818a18.905 18.905 0 0 0 12.027 4.286c10.5 0 19.052-8.55 19.052-19.052C49 8.552 40.449 0 29.948 0zm0 3.81c8.44 0 15.242 6.803 15.242 15.242 0 8.44-6.803 15.242-15.242 15.242a15.214 15.214 0 0 1-15.242-15.242c0-8.44 6.802-15.242 15.242-15.242z"
                                />
                                <path d="M23.341 11L21 13.341l6.159 6.159L21 25.659 23.341 28l6.159-6.159L35.659 28 38 25.659 31.841 19.5 38 13.341 35.659 11 29.5 17.159z" />
                            </g>
                        </svg>
                        <div className="title">{m_title}</div>
                        <div className="text">{text_1}</div>
                        <form>
                            <input type="text" placeholder={placeholder_2} />
                            <input type="text" placeholder={placeholder_3} />
                            <input type="text" placeholder={placeholder_4} />
                            <button type="submit" className="button">
                                {text_2}
                            </button>
                        </form>
                    </div>
                </div>
            </div>
        );
    }
}

export default App;



